.site-header {
	display: flex;
	justify-content: space-between;
	max-width: 100%;
	padding: 5vw 5vw;

	.left {
		display: flex;
		width: 100%;
		justify-content: space-between;

		.logo-wrapper {
			margin-right: 25px;
			.site-title {
				font-family: Arial,sans-serif;
				font-weight: 400;
				font-size: 32px;
				line-height: 1.25em;
				letter-spacing: .02em;
				font-family: aktiv-grotesk;
				font-weight: 700;
				font-style: normal;
				font-size: 16px;
				line-height: 1.2em;
				letter-spacing: .05em;
				text-transform: uppercase;
				a {
					text-decoration: none;
					color: #111;
				}
			}
		}

		.nav-wrapper {
			flex: 0 1 auto;
			.site-navigation {
				display: flex;
				font-family: aktiv-grotesk;
				font-weight: 400;
				font-style: normal;
				font-size: 14px;
				letter-spacing: .04em;
				text-transform: none;
				line-height: 1em;
				.nav-item {
					display: inline-block;
					margin: 0;
					margin-right: 15px;
					
					.nav-item-index {
						color: #111;
						text-decoration: none;
						
					}

					&:last-of-type {
						margin-right: 0;
					}
				}
			}
		}
	}
}


