.site-page {
	margin-bottom: 5vw;
    padding-right: 5vw;
	padding-left: 5vw;
	width: auto;
    max-width: 100%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
	box-sizing: border-box;

	height: calc(100% - 300px);

	.horizontal-centered {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	.page-text-wrapper {
		max-width: 350px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 7.5vh;

		.page-title {
			font-family: Arial,sans-serif;
			font-weight: bold;
			font-style: normal;
			font-size: 20px;
			line-height: 1.3em;
			letter-spacing: .02em;
			text-transform: none;
			color: #111;
			margin-bottom: 0;

			&.align-center {
				text-align: center;
			}
			&.align-right {
				text-align: right;
			}
		}
		.page-description {
			font-family: proxima-nova;
			font-weight: 400;
			font-style: normal;
			font-size: 16px;
			line-height: 1.5em;
			letter-spacing: 0em;
			text-transform: none;
			color: rgba(18,18,18,.9);
		}
	}

	.react-photo-gallery--gallery {
		margin-bottom: 15vh;
	}
	.main-content {
		height: 100%;
		.category {
			position: relative;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			text-decoration: none;
			color: white;
			margin: 12px;

			.category-title {
				position: absolute;
				font-family: Arial,sans-serif;
				font-weight: 400;
				letter-spacing: .02em;
				text-transform: uppercase;
				font-style: normal;
				font-size: 18px;
				line-height: 1.4em;
				opacity: 0;
				margin-bottom: 0;
				display: inline-block;
				max-width: 70%;

				transition: .3s ease-in-out;
			}
			
			.category-overlay {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				opacity: 0;
				transition: .3s ease-in-out;
			}

			&:hover {
				.category-title {
					opacity: 1;
				}

				.category-overlay {
					opacity: 0.8;
				}
			}
		}
	}
}
