.footer {
	padding: 5vw 5vw;
	margin-top: 8vw;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;

	font-family: aktiv-grotesk;
    font-weight: 500;
    font-style: normal;
    font-size: 20px;
    letter-spacing: .02em;
    text-transform: none;
	.footer-item {
		text-decoration: none;
		color: #ccc;
		margin-right: 1rem;

		transition: color .3s ease-in-out;
		&:last-of-type {
			margin-right: 0;
		}

		&:hover,
		&.active {
			color: #111;
		}
	}
}